//@import "sb-normalize.css";
@import url('https://fonts.googleapis.com/css?family=Josefin+Sans:400,700');
//font-family: 'Josefin Sans', sans-serif;
@font-face {
  font-family: 'Aleo'; /* Имя шрифта */
  src: url("../fonts/Aleo-Font/Webfont Kit/Regular/aleo-regular-webfont.svg"); /* Путь к файлу со шрифтом */
}
$text-color: #fff;
$form-border-color: $text-color;
$button-color-blue: #5474fd;
$header-bg-blue: $button-color-blue;
$placeholder-color: #999999;
$input-color: #292929;
$s02-bg-color: #333333;
@mixin setFont($font:'Arial', $color:#000, $size:18px, $weigth:400, $spacing:normal, $line-height:26px) {
  //$spacing in em
  font: $weigth $size $font, sans-serif;
  line-height: $line-height;
  letter-spacing: $spacing;
  color: $color;
}
@mixin setBackground($url, $color:#000, $cover:cover, $repeat:no-repeat) {
  //$url should be in ""
  background: $color url($url) $repeat;
  background-size: $cover;
}
@mixin setFlex($justify-content: flex-start, $align-item: center, $direction: row) {
  display: flex;
  justify-content: $justify-content;
  align-items: $align-item;
  flex-direction: $direction;
}
@mixin setBlackBox {
  & .inner {
    height: 100%;
    @include setFlex(center, flex-start, column);
  }
}
@mixin cfx() {
  &:before,
  &:after {
    content: '';
    display: block;
    clear: both;
  }
}
@mixin hover-shadow() {
  &:hover {
    text-shadow:  0 0 5px rgba(255,255,255,0.75);
  }
}
@mixin hover($bg) {
  &:hover {
    background: lighten($bg, 10);
    -webkit-box-shadow: 0 0 5px rgba(255,255,255,0.5);
    -moz-box-shadow:  0 0 5px rgba(255,255,255,0.5);
    box-shadow:  0 0 5px rgba(255,255,255,0.5);
  }
}
@mixin active() {
  &:active {
    -webkit-transform: scale(0.9);
    -moz-transform: scale(0.9);
    -ms-transform: scale(0.9);
    -o-transform: scale(0.9);
    transform: scale(0.9);
  }
}
@mixin makeColorButton($color: darkred, $padding:25px 35px,) {
  display: inline-block;
  padding: $padding;
  background: $color;
  color: #fff;
  text-transform: uppercase;
  border: 0;
  @include hover($color);
  @include active();
}
* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
*:focus {
  border: 0;
}
a {
  display: inline-block;
  color: #000;
  text-decoration: none;
}
.arrow-down {
  fill: $text-color;
}
.inner {
  width: 1170px;
  margin: 0 auto;
}
.s01 {
  @include setBackground("../img/s01-bg.jpg");
  height: 100vh;
  min-height: 500px;
  text-align: center;
  border: 1px solid transparent;
  &__title {
    @include setFont('Josefin Sans', $text-color, 48px, 700, 0.1em);
    text-transform: uppercase;
    margin-top: 265px;
  }
  &__subtitle{
    @include setFont('Josefin Sans', $text-color, 22px, 400, 0.31em);
    margin: 10px 0 50px;
  }
  &__scroll {
    @include setFont('Josefin Sans', $text-color, 14px, 400, 0.2em);
    @include hover-shadow();
    @include active();
    display: block;
    margin-top: 110px;
  }
}
.search {
  display: inline-block;
  width: 652px;
  height: 62px;
  border: 1px solid $form-border-color;
  background: $form-border-color;
  text-align: left;
  padding: 0;
  &__input {
    width: calc(100% - 160px);
    height: 100%;
    border: 0;
    border-left: 3px solid transparent;
    vertical-align: middle;
    text-align: center;

    @include setFont('Josefin Sans', $input-color, 14px, 400, 0.2em);
    &:focus {
      outline: none;
      border-left: 3px solid $button-color-blue;
    }
    &::placeholder {
      text-align: center;
      color: $placeholder-color
    }
  }
  &__button {
    width: 150px;
    height: 100%;
    vertical-align: middle;
    float: right;
    @include setFont('Aleo', $text-color, 14px, 400, 0.2em);
    @include makeColorButton($button-color-blue, 15px 20px);
    
    & svg {
      fill: $text-color;
    }  
  }
}
.head {
  height: 80px;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background: $header-bg-blue;
  & .inner{
    height: 100%;
    @include setFlex(space-between)
    //display: flex;
    //justify-content: space-between;
    //align-items: center;
  }
  &__logo {
    @include setFont('Josefin Sans', $text-color, 24px, 700, 0.1em);
    @include hover-shadow();
    @include active();
    text-transform: uppercase;
  }
  & .menu {
    float: right;
  }
}
.menu {
  &__item {
    @include setFont('Aleo', $text-color, 14px, 400);
    @include hover-shadow();
    @include active();
    text-transform: uppercase;
    margin-left: 26px;
    &:first-child {
      margin-left: 0;
    }
  }
}
.s02 {
  @include setBackground("../img/s02-bg.jpg", $s02-bg-color, contain);
  height: 1020px;
  & .inner {
    height: 100%;
    //@include setFlex(space-around)
    //display: flex;
    //justify-content: space-around;
    //align-items: center;
  }
  & .row {
    height: 800px;
    & [class^='col'] {
      @include setFlex(center, center, column);
      @include setFont('Josefin Sans', $text-color, 16px, 400, 0.1em);
      //display: flex;
    }
  }
  &__title {
    @include setFont('Aleo', $text-color, 36px, 400);
  }
  &__text {
    @include setFont('Josefin Sans', $text-color, 16px, 400, 0.1em);
    padding: 60px 0 40px;
  }
  &__button {
    @include makeColorButton($button-color-blue, 15px 20px);
  }
  &__img:nth-of-type(2n) {
    margin-left: 40%;
  }
}
.row{
  width: 100%;
  @include cfx();
  & .w170 {
    width: 170px;
  }
  & [class^='col'] {
    padding: 0 50px;
    display: inline-block;
    float: left;
    *--p25 {
      padding: 0 25px;
    }
    &:first-child {
      padding-left: 0;
    }
    &:last-child {
      padding-right: 0;
    }
  }
  & .col-2 {
    width: 50%;
  }
  & .col-3 {
    width: 33.333%;
  }
  & .col-5 {
    width: 20%;
  }
}
.s03 {
  height: 700px;
  @include setBackground("../img/s03-bg.jpg");
  text-align: left;
  @include setBlackBox();
}
.s04 {
  height: 700px;
  @include setBackground("../img/s04-bg.jpg");
  text-align: right;
  @include setBlackBox();
}
.black-box {
  width: 540px;
  /*height: 300px;*/
  padding: 50px 25px;
  background-color: rgba(0,0,0,0.75);
  &--right {
    align-self: flex-end;
  }
  &__subtitle {
    @include setFont('Josefin Sans', $text-color, 14px, 400, 0.14em);
  }
  &__title {
    @include setFont('Josefin Sans', $text-color, 24px, 700, 0.14em);  
    margin: 10px 0;  
  }
  &__text {
    @include setFont('Josefin Sans', $text-color, 14px, 400, 0.14em);
  }
  &__button {
    @include makeColorButton($button-color-blue, 20px 60px); 
    margin: 15px 0 0 0; 
  }
}
.s05 {
  height: 730px;
  border: 1px solid transparent;
  text-align: center;
  @include setBackground("../img/s05-bg.jpg");
  &__title {
    @include setFont('Aleo', $text-color, 36px);
    margin: 110px 0 0 0;
  }
  &__content {
    width: 100%;
    margin: 95px 0 0 0;
    background: #333333;
    @include cfx();
  }  
  &__image {
    float: left;
  }
  &__info {
    padding: 50px 60px;
    text-align: left;
    float: right;
    width: 61.9%;
  }
  &__author {
    @include setFont('Josefin Sans', $text-color, 20px);    
  }
  &__text {
    @include setFont('Josefin Sans', $text-color, 16px);
    margin: 30px 0 0 0;
  }
}
.foot {
  height: 265px;
  text-align: center;
  border: 1px solid transparent;
  background: #232323;
  &__title {
    @include setFont('Josefin Sans', $text-color, 24px, 700, 0.14em);
    margin: 115px 0 0 0;
  }
  &__subtitle {
    @include setFont('Josefin Sans', $text-color, 16px, 400, 0.1em);
    margin: 10px 0 0 0;
  }
  & .fab {
    font-size: 16px;
    margin: 35px 0 0 0;
  }
}